.nav-list-header ul li {
    display: inline-block;
    margin: 0 8px;
    list-style: none;
    font-size: 16px;
    margin-bottom: 0px;

}

.nav-list-header ul a {
    margin: 0 12px;
    font-weight: bold;
    color: #7b2abf;

}

.nav-list-header ul a:active {
    color: #7b2abf;

}



.activeHamburguer div {
    transition: all 0.2s linear !important;
    opacity: 1 !important;
    transform: rotate(45deg) translate(0px, 4px) !important;

}


.activeHamburguer div:nth-child(2) {
    transform: rotate(-45deg) translate(0px, -4px) !important;
}

.activeHamburguer div:nth-child(3) {
    display: none !important;
}