.circle-image {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    overflow: hidden;
    display: inline-block;
    position: relative;
}

.content-image {
    width: 100%;
    /* background: #f9f9f9; */
    padding: 12px;
    text-align: center;

}

.content-info {
    padding: 8px 24px;
}

.favorite {
    /* border: 1px solid #7b2abf; */
    opacity: 0.3;
}

.btnAdd:disabled {
    background: #9264b9 !important;
    opacity: 0.5;
    cursor: none !important;
}


@media (max-width: 1181px) {
    .card-item {
        width: 345px !important;
    }
}


@media (max-width: 1025px) {
    .card-item {
        width: 293px !important;
    }
}

@media (max-width: 1000px) {
    .card-item {
        width: 44% !important;
    }
}

@media (max-width: 650px) {
    .card-item {
        width: 100% !important;
    }
}