.nav-list ul li {
    display: inline-block;
    margin: 0 8px;
    list-style: none;
    margin-bottom: 32px;
    font-size: 16px;


}

.nav-list ul a {
    margin: 0 12px;
    font-weight: bold;


}

.nav-list ul a:active {
    color: #7b2abf;

}

.NavMobileactive {
    left: 0 !important;
    transition: 0.3s ease-in !important;
}